.main-header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
    padding: 0;
    
    .container {
        max-width: 1600px;
        margin: 0 auto;
        padding: 0 15px;
        
        @media #{$mq-2xl-max} {
            max-width: 1440px;
        }
        
        @media #{$mq-xl-max} {
            max-width: 1200px;
        }
        
        @media #{$mq-lg-max} {
            width: 100%;
            max-width: 100%;
            padding: 0;
        }
    }
    
    .header-wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 8rem;
        background-color: rgba(30, 113, 184, 0.5);
        backdrop-filter: blur(0px);
        border-radius: 0 0 20px 20px;
        padding: 0 2rem;
        box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
        
        @media #{$mq-md-max} {
            height: 5.5rem;
            padding: 0 1rem;
        }
    }
    
    .logo {
        width: 120px;
        
        img {
            width: 100%;
            height: auto;
        }
        
        @media #{$mq-md-max} {
            width: 100px;
        }
    }
    
    .nav-and-actions {
        display: flex;
        align-items: center;
        margin-left: auto;
        
        @media #{$mq-lg-max} {
            margin-left: 0;
            
            /* Only show header-actions in mobile nav when menu is active */
            .header-actions {
                display: none;
            }
        }
    }
    
    .main-nav {
        display: flex;
        
        @media #{$mq-lg-max} {
            position: fixed;
            top: 0;
            right: -100%;
            width: 100%;
            height: 100vh;
            background-color: rgba(10, 65, 119, 0.95);
            z-index: 100;
            padding: 100px 30px 30px;
            transition: right 0.3s ease;
            overflow-y: auto;
            margin: 0;
            
            &.active {
                right: 0;
                
                /* When nav is active, show the header actions inside it */
                & + .header-actions {
                    display: block;
                    position: fixed;
                    top: 150px;
                    right: 30px;
                    z-index: 101;
                }
            }
        }
        
        ul {
            display: flex;
            list-style: none;
            margin: 0;
            padding: 0;
            
            @media #{$mq-lg-max} {
                flex-direction: column;
            }
            
            li {
                margin: 0 15px;
                
                @media #{$mq-lg-max} {
                    margin: 0 0 20px;
                }
                
                a {
                    color: $white;
                    text-decoration: none;
                    font-weight: $medium;
                    font-size: 16px;
                    transition: color 0.3s ease;
                    position: relative;
                    
                    &::after {
                        content: '';
                        position: absolute;
                        width: 0;
                        height: 2px;
                        bottom: -4px;
                        left: 0;
                        background-color: #38c172;
                        transition: width 0.3s ease;
                    }
                    
                    &:hover {
                        color: $white;
                        
                        &::after {
                            width: 100%;
                        }
                    }
                    
                    @media #{$mq-lg-max} {
                        font-size: 20px;
                        display: block;
                    }
                }
            }
        }
    }
    
    .header-actions {
        display: flex;
        align-items: center;
        margin-left: 30px;
        
        @media #{$mq-lg-max} {
            margin-left: 0;
        }
        
        .search-icon {
            margin-right: 20px;
            cursor: pointer;
            
            img {
                width: 20px;
                height: 20px;
            }
            
            @media #{$mq-xs-max} {
                margin-right: 10px;
            }
        }
    }
    
    .header-cta {
        .btn-primary {
            display: inline-block;
            padding: 0.75rem 1.5rem;
            background-color: #0046ff;
            color: $white;
            text-decoration: none;
            border-radius: 0.25rem;
            font-weight: $medium;
            font-size: 1rem;
            transition: background-color 0.3s ease;
            text-align: center;
            
            &:hover {
                background-color: darken($blue, 10%);
            }
            
            @media #{$mq-md-max} {
                padding: 0.5rem 1rem;
                font-size: 0.875rem;
            }
            
            @media #{$mq-xs-max} {
                display: none;
            }
        }
    }
    
    .mobile-toggle {
        display: none;
        flex-direction: column;
        justify-content: space-between;
        width: 30px;
        height: 20px;
        cursor: pointer;
        z-index: 200;
        margin-left: 20px;
        
        span {
            display: block;
            width: 100%;
            height: 2px;
            background-color: $white;
            transition: all 0.3s ease;
        }
        
        @media #{$mq-lg-max} {
            display: flex;
        }
        
        &.active {
            span {
                &:nth-child(1) {
                    transform: translateY(9px) rotate(45deg);
                }
                
                &:nth-child(2) {
                    opacity: 0;
                }
                
                &:nth-child(3) {
                    transform: translateY(-9px) rotate(-45deg);
                }
            }
        }
    }
}

// Add body lock for when menu is active
body {
    &.menu-open {
        overflow: hidden;
    }
}
