body{
    font-family: $font-body;
}

.main-header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
    //backdrop-filter: blur(10px);
    
    .header-wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 8rem;
        
        @media #{$mq-md-max} {
            height: 70px;
        }
    }
    
    .logo {
        width: 120px;
        
        img {
            width: 100%;
            height: auto;
        }
        
        @media #{$mq-md-max} {
            width: 100px;
        }
    }
    
    .mobile-toggle {
        display: none;
        flex-direction: column;
        justify-content: space-between;
        width: 30px;
        height: 20px;
        cursor: pointer;
        z-index: 200;
        
        span {
            display: block;
            width: 100%;
            height: 2px;
            background-color: $white;
            transition: all 0.3s ease;
        }
        
        @media #{$mq-lg-max} {
            display: flex;
        }
        
        &.active {
            span {
                &:nth-child(1) {
                    transform: translateY(9px) rotate(45deg);
                }
                
                &:nth-child(2) {
                    opacity: 0;
                }
                
                &:nth-child(3) {
                    transform: translateY(-9px) rotate(-45deg);
                }
            }
        }
    }
    
    .main-nav {
        @media #{$mq-lg-max} {
            position: fixed;
            top: 0;
            right: -100%;
            width: 100%;
            height: 100vh;
            background-color: $black;
            z-index: 100;
            padding: 100px 30px 30px;
            transition: right 0.3s ease;
            overflow-y: auto;
            
            &.active {
                right: 0;
            }
        }
        
        ul {
            display: flex;
            list-style: none;
            margin: 0;
            padding: 0;
            
            @media #{$mq-lg-max} {
                flex-direction: column;
            }
            
            li {
                margin: 0 15px;
                
                @media #{$mq-lg-max} {
                    margin: 0 0 20px;
                }
                
                a {
                    color: $white;
                    text-decoration: none;
                    font-weight: $medium;
                    font-size: 16px;
                    transition: color 0.3s ease;
                    
                    &:hover {
                        color: $gray-light;
                    }
                    
                    @media #{$mq-lg-max} {
                        font-size: 20px;
                        display: block;
                    }
                }
            }
        }
    }
  
}

// Add body lock for when menu is active
body {
    &.menu-open {
        overflow: hidden;
    }
}

.about-section{
    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .about-content{
        color: #333;

        h2{
            color: #1a1a1a;
            line-height: 1.2;
            font-size: 2.5rem;
        }

        p{
            color: #333;
            line-height: 1.5;
            font-size: 1.1rem;
        }

        .btn-saiba-mais{
            font-size: 1.1rem;
            
        }
    }

    
.anuncie-section {
    position: relative;
    background: linear-gradient(90deg, #0066B3 0%, #1B9B54 100%);
    overflow: hidden;

    .anuncie-bg {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        background: url('../img/elementos.png') no-repeat left center;
        background-size: cover;
        pointer-events: none;
        z-index: 1;
    }

    .container-fluid {
        position: relative;
        z-index: 2;

        .container {

            .anuncie-container {
                max-width: 960px;
                margin: 0 auto;
                padding: 2.5rem 0;
            }

            .anuncie-flex {
                display: flex;
                justify-content: space-between;
                align-items: center;
                gap: 2rem;
                width: 100%;
                position: relative;
                z-index: 2;

                > div {
                    h2{
                        font-size: 3rem;
                        color: $white;
                    }

                    p{
                        font-size: 1.2rem;
                        color: $white;
                    }
                }

                .anuncie-text {
                    flex: 1 1 0;
                    text-align: left;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }

                .anuncie-btn {
                    display: block;
                    background-color: #48C672;
                    color: #fff;
                    border: none;
                    border-radius: 10px;
                    font-weight: 500;
                    width: 250px;
                    text-align: center;
                    font-size: 1.15rem;
                    padding: .9rem 2rem;
                    transition: background 0.2s, box-shadow 0.2s;
                    box-shadow: none;
                    text-decoration: none;
                    text-align: center;

                    &:hover,
                    &:focus-visible {
                        background-color: #43a047;
                        color: #fff;
                        text-decoration: none;
                    }
                }
            }
        }
    }

    @media (max-width: 991.98px) {
        .container-fluid {
            .container {
                .anuncie-flex {
                    flex-direction: column;
                    align-items: stretch;
                    gap: 1.5rem;
                    text-align: center;

                    .anuncie-text {
                        justify-content: center;
                        text-align: center;
                    }
                    .anuncie-btn {
                        min-width: unset;
                        width: 100%;
                    }
                }
            }
        }
    }
}

.cases-section {
    padding: 2.5rem 0;

    .cases-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;

        h2{
            font-size: 2.5rem;
            color: $black;
        }
    }
    
    .cases-btn {
        display: inline-block;
        padding: 1rem 1.5rem;
        font-size: 1rem;
        font-weight: 700;
        color: #1976d2;
        background: #fff;
        border: 1px solid #1976d2;
        border-radius: 8px;
        text-decoration: none;
        transition: background 0.2s, color 0.2s, box-shadow 0.2s;
        box-shadow: none;
        text-align: center;
    
        &:hover,
        &:focus {
            background: #1976d2;
            color: #fff;
            text-decoration: none;
            box-shadow: 0 2px 8px 0 rgba(25, 118, 210, 0.08);
        }
    }

    .cases-swiper {
        padding-top: 4rem;

        .swiper-pagination{
            margin-top: 4rem;
            position: relative;
        }
    }

    .swiper-slide {
        // Espao para customizao do slide, se necessrio
    }

    .case-link {
        display: block;
        text-decoration: none;
        color: inherit;

        .case-card {
            transition: transform 0.3s cubic-bezier(.4,1.5,.5,1), box-shadow 0.3s;
            will-change: transform;
        }

        &:hover .case-card,
        &:focus .case-card {
            transform: translateY(-12px) scale(1.03);
            box-shadow: 0 12px 32px 0 rgba(0,0,0,0.15);
        }
    }


    .case-logo {
        position: absolute;
        top: -2rem;
        left: 20px;
        z-index: 2;
        background: white;
        border-radius: 12px;
        padding: 10px;
        width: 60px;
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #c3c3c3;

        img {
            max-width: 100%;
            max-height: 100%;
        }
    }

    .case-image {
        position: relative;
        height: 400px;
        border-radius: 20px;
        overflow: hidden;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .case-overlay {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        background: linear-gradient(transparent, rgba(0,0,0,0.8));
        padding: 20px;

        h3 {
            color: #fff;
            margin-bottom: 1.5rem;
        }

        .btn {
            &.btn-link {
                color: orangered;
                padding: 0;
                font-size: 2.5rem;
                text-decoration: none;
                background-color: transparent;
                border: none;

                &:hover,
                &:focus {
                    text-decoration: underline;
                }
            }
        }
    }
}

.instituto-ric{
    padding: 1rem 0;
    margin: 0 auto;
    margin-top: 3rem;

    .instituto-flex {
        display: flex;
        align-items: center;
        gap: 2rem;
        width: 100%;
        padding: 2rem 0;
        border-radius: 1rem;
        justify-content: center;
    
        .instituto-logo {
            max-width: 400px;
            flex: 0 0 auto;
            img {
                width: 100%;
                height: auto;
                display: block;
            }
        }
    
        .instituto-content {
            flex: 1 1 0;
            color: #fff;
            max-width: 800px;
            padding-left: 6rem;
            padding-right: 6rem;
            h2 {
                font-size: 2.5rem;
                font-weight: 700;
                margin-bottom: 1.5rem;
                margin-top: 0;
            }
            p {
                font-size: 1.15rem;
                margin-bottom: 2rem;
                line-height: 1.5;
            }

            .instituto-btn .btn-content {
                display: inline-flex;
                align-items: center;
                gap: 0.5rem;
                vertical-align: middle;
            }
            
        }
    }
    
    @media (max-width: 991.98px) {
        .instituto-flex {
            flex-direction: column;
            align-items: center;
            text-align: center;
            .instituto-logo {
                margin-bottom: 2rem;
            }
            .instituto-content {
                h2 {
                    font-size: 2rem;
                }
            }
        }
    }
}

.insights-section {
    margin: 3rem 0;

    a{
        display: block;
        text-decoration: none;
    }

    .card {
        border-radius: 1.5rem;
        overflow: hidden;
        border: none;
        background: transparent;
    }

    .card-img-overlay {
        background: linear-gradient(180deg, rgba(0,0,0,0) 40%, rgba(0,0,0,0.8) 100%);
        border-radius: 1.5rem;
        padding: 2rem;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
    }

    .badge {
        font-size: 1.5rem;
        font-weight: 600;
        border-radius: 0.5rem;
        padding: 1rem;
        border-radius: 10px;
        width: 130px;
        text-align: center;

        &.curso{
            color: $white;
            background-color: rgba(255, 69, 0, 0.85);
        }

        &.artigo{
            color: $white;
            background-color: rgba(30, 113, 184, 0.85);
        }

        &.estudo{
            color: $white; 
            background-color: rgba(35, 165, 79, 0.85);
        }
    }

    .text-white{
        color: $white;
    }

    .btn-link {
        color: #ff4500;
        padding: 0;
        font-size: 2.5rem;
        text-decoration: none;
        background-color: transparent;
        border: none;
        text-align: left;
    }

    .card img {
        border-radius: 1.5rem;
        object-fit: cover;
        width: 100%;
        height: 100%;
    }

    .card-bg-img {
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        border-radius: 1.5rem;
        overflow: hidden;
        width: 100%;
        position: relative;
        min-height: 240px; // altura mnima para os cards pequenos
    }

    // Card grande
    .col-lg-7 .card-bg-img {
        height: 700px;
        min-height: 700px;
    }

    // Card pequeno
    .col-lg-5 .card-bg-img {
        height: 340px;
        min-height: 340px;
    }

    .col-lg-5 .row.g-4 {
        gap: 1.5rem;
    }

    .justify-content-between{
        margin-bottom: 2rem;
    }

    h2{
        font-size: 2.5rem;
        margin: 0;
        margin-top: 2rem;
    }

    .justify-content-lg-end{
        display: flex;
        justify-content: right;
        align-items: center;

        a{
            display: block;;
            font-size: 1.15rem;
            padding: .9rem 2rem;
            transition: background .2s, box-shadow .2s;
            box-shadow: none;
            text-decoration: none;
            text-align: center;
            border-radius: 10px;
            border: 1px solid #0066cc;
            margin-left: 1rem;
            color: #0066cc;
            font-weight: bold;

            &.active{
                background-color: #0066cc;
                color: $white;
            }

            &:hover{
                background-color: #0066cc;
                color: $white; 
            }
        }
    }

    h3{
        font-size: 1.5rem;
    }

    p{
        font-size: 1.1rem;
    }
}
}
